.what-to-say__heading {
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  font-size: 28px;
  margin-bottom: 20px;
}

.what-to-say__heading--contacts {
  margin: 0 0 22px;
}

.what-to-say__fieldset {
  padding: 0;
  margin: 0 0 10px 0;
  display: flex;
  flex-wrap: wrap;
  border: none;
}

.what-to-say__field {
  padding: 8px;
  margin: 0;
  flex-basis: 660px;
  font-size: 16px;
  font-family: inherit;
  border: 1px solid @main-text-opacity-color;
  color: @third-text-color;
  box-sizing: border-box;
}

.what-to-say__button.button {
  margin: 0 37% 78px;
  font-size: 20px;
  min-width: 160px;
  padding: 22px 22px 22px 41px;
  color: @button-text-color;
  font-family: inherit;
  position: relative;

